import React from 'react';

export default class Skills extends React.Component {
  constructor(props) {
    super(props);
    this.skillsData = [
      {skill: "Angular", description: "I'm an expert in building pixel perfect, data-driven, UIs with AngularJs"},
      {skill: "React", description: "yayaya react"},
      {skill: "Node", description: "yaya node"},
      {skill: "Docker", description: "yaya docker"},
      {skill: "PostgreSQL", description: "yaya postgres"},
      {skill: "MongoDB", description: "yayyaya mongodb"},
      {skill: "RabbitMQ", description: "yayyaya rabbitmq"}
    ];

    this.state = {
      currentSkill: this.skillsData[0]
    };
  }

  changeCurrentSkill(index) {
    this.setState({
      currentSkill: this.skillsData[index]
    });
  }

  render() {
    const skills = this.skillsData.map((d, i) => (
      <li key={i} onMouseOver={this.changeCurrentSkill.bind(this, i)}>{d.skill}</li>
    ))

    return (
      <div>
        <p>{this.state.currentSkill.description}</p>
        <div>{skills}</div>
      </div>
    )
  }
}



